<!--
 * @Description: 修改货车
 * @Version: Do not edit
 * @Autor: gitxinghui
 * @Date: 2020-12-01 01:49:59
 * @LastEditors: gitxinghui
 * @LastEditTime: 2020-12-22 08:22:13
-->
<template>
    <view class="things_publish_car">
        <!-- tabbar -->
        <cu-custom><block slot="content">货车更新</block></cu-custom>
        <view class="car_form">
            <form @submit="formSubmit">
                <!-- 地址 -->
                <view class="things_width_full address_box">
                    <view class="flex justify-between align-center cu-form-group start_address" @tap="wPickerStart.visible=true">
                        <view class="flex justify-start align-center title title_not"><view class="before_circle"></view>{{wPickerStart.value ? wPickerStart.value : '输入装载地点'}}</view>
                        <input type="text" class="hide" name="loading_region" :value="wPickerStart.value"/>
                    </view>
                    <view class="flex justify-between align-center cu-form-group end_address" @tap="wPickerEnd.visible=true">
                        <view class="flex justify-start align-center title title_not"><view class="before_circle"></view>{{wPickerEnd.value ? wPickerEnd.value : '输入卸货地点'}}</view>
                        <input type="text" class="hide" name="unloading_region" :value="wPickerEnd.value"/>
                    </view>
                </view>
                <view class="tip_title">填写基本信息</view>
                <view class="things_width_full form_info">
                    <view class="cu-form-group">
                        <view class="title">运输价格(单价)：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" type="digit" name="unit_price" :value="car_form_data.unitPrice"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">车辆类型：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" name="car_type_label" :disabled="true" :value="wPickerCar.label" @tap="wPickerCar.visible=true"/>
                        <input class="hide" name="car_type" :value="wPickerCar.value"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">车牌号：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" name="car_number_label" :value="carPicker.car_number" :disabled="true" @tap="openPlatePicker(1)"/>
                        <input class="hide" name="car_number" :value="carPicker.car_number"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">挂车牌号：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" name="listing_car_number_label" :value="carPicker.listing_car_number" :disabled="true" @tap="openPlatePicker(2)"/>
                        <input class="hide" name="listing_car_number" :value="carPicker.listing_car_number"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">车内灌质：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" name="car_filling" :value="car_form_data.carFilling"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">灌体立方数： </view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" name="cubic_number" :value="car_form_data.cubicNumber"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">重量(吨)：</view>
                        <input class="inputText" placeholder="请填写" type="digit" placeholder-class="placeHolderText" name="weight" :value="car_form_data.weight"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">开始时间：</view>
                        <picker name="start_time" mode="date" :value="start_time" start="2015-09-01" @change="startDateChange">
                            <view class="flex justify-start align-center picker" :class="start_time ? 'inputText':'placeHolderText'">{{start_time ? start_time : '请选择'}}</view>
                        </picker>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">结束时间：</view>
                        <picker name="end_time" mode="date" :value="end_time" start="2015-09-01" @change="endDateChange">
                            <view class="flex justify-start align-center picker" :class="end_time ? 'inputText':'placeHolderText'">{{end_time ? end_time : '请选择'}}</view>
                        </picker>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">车辆所属公司：</view>
                        <input class="inputText" placeholder="请填写" placeholder-class="placeHolderText" name="car_company" :value="car_form_data.carCompany"/>
                    </view>
                    <view class="cu-form-group">
                        <view class="title">折扣比例：</view>
                        <input class="inputText" placeholder="请选择折扣比例" placeholder-class="placeHolderText" :disabled="true" name="radio_type_label" :value="wPickerDiscount.label" @tap="wPickerDiscount.visible=true"/>
                        <input class="hide" name="radio_type" :value="wPickerDiscount.value"/>
                    </view>
                    <view class="cu-form-group remarks_input">
                        <view class="title">备注：</view>
                        <textarea class="inputText text_input" placeholder-class="placeHolderText" maxlength="-1" placeholder="请填写备注..." name="remark" :value="car_form_data.remark"></textarea>
                    </view>
                </view>
                <view class="flex justify-center align-center btn_box">
                    <!-- 更新货车 -->
                    <button class="cu-btn publish_btn" form-type="submit" :disabled="buttonType">更新</button>
                </view>
                <!-- 装载-城市三级联动框 -->
                <w-picker 
                    :visible.sync="wPickerStart.visible"
                    mode="region"
                    :value="wPickerStart.value"
                    default-type="name"
                    :level="3"
                    :hide-area="false"
                    @confirm="selectAddressStart($event,'region')"
                    themeColor="#32d08b"
                    ref="regionStart">
                </w-picker>
                <!-- 卸载-城市三级联动框 -->
                <w-picker 
                    :visible.sync="wPickerEnd.visible"
                    mode="region"
                    :value="wPickerEnd.value"
                    default-type="name"
                    :level="3"
                    :hide-area="false"
                    @confirm="selectAddressEnd($event,'region')"
                    themeColor="#32d08b"
                    ref="regionEnd">
                </w-picker>
                <!-- 货车类型 -->
                <w-picker 
                    :visible.sync="wPickerCar.visible"
                    mode="selector"
                    :value="wPickerCar.value"
                    default-type="name"
                    :default-props="wPickerCar.defaultProps"
                    :options="wPickerCar.list"
                    @confirm="selectCar($event,'selector')"
                    themeColor="#32d08b"
                    ref="carPicker">
                </w-picker>
                <!-- 折扣类型 -->
                <w-picker 
                    :visible.sync="wPickerDiscount.visible"
                    mode="selector"
                    :options="base_discountData"
                    @confirm="selectDiscount($event,'selector')"
                    themeColor="#32d08b"
                    ref="discountPicker">
                </w-picker>
                <!-- 车牌号输入 -->
                <plateInput v-if="carPicker.plateShow" :plate="carPicker.plateNo" @export="setPlate" @close="carPicker.plateShow = false"/>
            </form>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
    import { getTruckCollectionInfo_api,updateTruckCollection_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            // 全局按钮状态
            buttonType: false,
            appBaseData:null,//全局配置信息
            userInfo: {},
            truck_id: '',
            wPickerStart: {
                visible: false,
                result: {
                    value: ''
                },
                value: ["广东省","茂名市","茂南区"],
                defaultRegion:["广东省","茂名市","茂南区"]
            },
            wPickerEnd: {
                visible: false,
                value: ["广东省","茂名市","茂南区"],
                defaultRegion:["广东省","茂名市","茂南区"]
            },
            // 货车类型
            wPickerCar: {
                visible: false,
                value: '',
                list: storage.get("appBaseData").wxapp.car_type,
                defaultProps: {"label":"name","value":"value"}
            },
            // 货车型号输入
            carPicker: {
                plateShow: false,
                plateNo: '',
                car_number:'',
                listing_car_number: '',
                type: 1,// 1:车牌号，2：挂车牌号
            },
            // 折扣类型
            wPickerDiscount: {
                visible: false,
                value: '',
                label: '',
                list: [],
            },
            // 开始时间
            start_time: '',
            // 结束时间
            end_time: '',
            car_form_data: null
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.getCarInfo();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_appBaseData': function(val) {
            that.appBaseData = val;
            that.wPickerCar.list = val.wxapp.car_type;
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    computed: {
        ...mapGetters(['base_discountData']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'user',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.car_form_data = null;
        },
        /**开始时间选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        startDateChange(e) {
            that.start_time = e.detail.value;
        },
        /**结束时间选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        endDateChange(e) {
            that.end_time = e.detail.value;
        },
        /**装货地点选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectAddressStart(e) {
            that.wPickerStart.value = e.result;
        },
        /**卸货地点选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectAddressEnd(e) {
            that.wPickerEnd.value = e.result;
        },
        /**打开车牌输入框
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        openPlatePicker(e) {
            that.carPicker.type = e;
            if (e==1) {
                that.carPicker.plateNo = that.carPicker.car_number;
            }
            if (e==2) {
                that.carPicker.plateNo = that.carPicker.listing_car_number;
            }
            that.carPicker.plateShow = true;
        },
        /**设置车牌号
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        setPlate(e) {
            if (e.length >= 7) {
                if (that.carPicker.type === 1) that.carPicker.car_number = e;
                if (that.carPicker.type === 2) that.carPicker.listing_car_number = e;
            };
			that.carPicker.plateShow = false;
        },
        /**选择车
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectCar(e) {
            that.wPickerCar.label = e.result;
            that.wPickerCar.value = e.value;
        },
        /**选择折扣
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectDiscount(e) {
            that.wPickerDiscount.label = e.result;
            that.wPickerDiscount.value = e.value;
        },
        /**
         * @description: 提交表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        formSubmit(e) {
             //定义表单规则
            let rule = [
                {name:"loading_region", checkType : "notnull", checkRule:"",  errorMsg:"请选择装载地址"},
                {name:"unloading_region", checkType : "notnull", checkRule:"",  errorMsg:"请选择卸载地址"},
                {name:"unit_price", checkType : "notnull", checkRule:"",  errorMsg:"请输入每吨单价"},
                {name:"car_type_label", checkType : "morethanzero",  errorMsg:"请选择货车类型"},
                {name:"car_number", checkType : "notnull", checkRule:"",  errorMsg:"请输入车牌号"},
                {name:"listing_car_number", checkType : "morethanzero",  errorMsg:"请输入挂牌号"},
                {name:"car_filling", checkType : "notnull", checkRule:"",  errorMsg:"请输入车内罐质"},
                {name:"cubic_number", checkType : "notnull", checkRule:"",  errorMsg:"请输入罐体立方数"},
                {name:"start_time", checkType : "notnull",checkRule:"",  errorMsg:"请选择装货时间"},
                {name:"end_time", checkType : "notnull", checkRule:"",  errorMsg:"请选择卸货时间"},
                {name:"car_company", checkType : "notnull", checkRule:"",  errorMsg:"请输入所属公司"},
                {name:"radio_type_label", checkType : "notnull", checkRule:"",  errorMsg:"请选择折扣比例"},
            ];
            //进行表单检查
            let formData = e.detail.value;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                return false;
            }
            formData.truck_id = that.truck_id;
            that.modal({name:'popLoading',value:true});
            that.base({name:'buttonType',value:true});
            updateTruckCollection_api(formData,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'更新成功',linkUrl:1}});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
            })
        },
        /**获取收藏的货车信息
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getCarInfo(e) {
            let params = {
                truck_id: that.truck_id
            };
            getTruckCollectionInfo_api(params,that).then(res=>{
                that.car_form_data = res;
                that.wPickerStart.value = res.loadingAddress;
                that.wPickerEnd.value = res.unloadingAddress;
                that.wPickerCar.value = res.carTypeId;
                that.wPickerCar.label = res.CarsType;
                that.carPicker.car_number = res.carNumber;
                that.carPicker.listing_car_number = res.listingCarNumber;
                that.start_time = res.startTime;
                that.end_time = res.endTime;
                that.wPickerDiscount.label = res.radioTypeText;
                that.wPickerDiscount.value = res.radioType;
            }).catch(err=>{
                console.log(err)
            })
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.truck_id || options.truck_id == ''|| options.truck_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该货车不存在',linkUrl:1}});
            return false;
        }
        that.truck_id = options.truck_id;
        that.getCarInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import './index';
</style>
